<template>
	<view>
		<cu-custom bgColor="bg-blue-one" :isBack="false" :isR="false">
			<block slot="content">商场</block>
		</cu-custom>
		<view class="bg-white goods_title" :style="[{height:titleheight+'px'}]">
			<!-- 轮播图 -->
			<view class="swiper bg-white">
				<view class="swiper-box">
					<swiper circular="true" autoplay="true" @change="swiperChange">
						<swiper-item v-for="swiper in swiperList" :key="swiper.id">
							<image :src="swiper.img"></image>
						</swiper-item>
					</swiper>
					<view class="indicator">
						<view class="dots" v-for="(swiper, index) in swiperList"
							:class="[currentSwiper >= index ? 'on' : '']" :key="index"></view>
					</view>
				</view>
			</view>
			<view class="cu-bar search">
				<view class="search-form round">
					<text class="cuIcon-search"></text>
					<input @focus="InputFocus" @blur="InputBlur" :adjust-position="false" type="text"
						placeholder="搜索关键词" confirm-type="search"></input>
				</view>
				<view class="action">
					<button class="cu-btn bg-blue-one shadow-blur round">搜索</button>
				</view>
			</view>
      <scroll-view
      scroll-x
      class="bg-white nav"
      :style="[{ top: CustomBar + 'px' }]"
    >
      <view class="flex text-center">
        <view
          class="cu-item flex-sub"
          :class="index == TabCur ? 'text-blue cur' : ''"
          v-for="(item, index) in tabNav"
          :key="index"
          @tap="tabSelect"
          :data-id="index"
        >
          {{ tabNav[index] }}
        </view>
      </view>
    </scroll-view>
			<!-- <view class="title">
				<image src="/static/hua.png"></image>
				猜你喜欢
				<image src="/static/hua.png"></image>
			</view> -->
		</view>
		<!-- 商品列表 -->
		<view class="goods-list" style="margin-top: 20px;">
			<view class="product-list">
				<view class="product" v-for="product in productList" :key="product.id" @tap="toGoods(product)">
					<view class="image">
						<image mode="widthFix" :src="product.picurl"></image>
					</view>
					<view class="name">{{ product.commodityInfo }}</view>
					<view class="info">
						<view class="price">{{ product.priceInfo }}</view>
						<view class="slogan">{{ product. payersnumber }}人付款</view>
					</view>
				</view>
			</view>
		</view>
		<view v-show="isLoadMore">
			<uni-load-more :status="loadStatus"></uni-load-more>
		</view>
	</view>
</template>

<script>
import {
  ListMoreDataMixin
} from '@/common/public-mixin.js';
var ttt = 0;

export default {
  mixins: [ListMoreDataMixin],
  data () {
    return {
      TabCur: 0,
      tabNav: ['人气', '销量', '价格', '筛选'],
      CustomBar: this.CustomBar,
      currentSwiper: 0,
      titleheight: 0,
      // 轮播图片
      swiperList: [{
        id: 1,
        src: 'url1',
        img: '../../static/gys/drawable-xxhdpi/faxian_banner.png'
      },
      {
        id: 2,
        src: 'url2',
        img: '../../static/gys/drawable-xxhdpi/dingdan_bannner.png'
      },
      ],
      Promotion: [],
      //猜你喜欢列表
      productList: [],
    };
  },
  //下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
  onPullDownRefresh () {
    setTimeout(() => {
      this.pageNo = 1
      this.productList = []
      this.getproductList()
      uni.stopPullDownRefresh()
    }, 3000);
  },
  //上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
  onReachBottom () {
    if (this.productList.length < 10) {
      this.isLoadMore = false
    } else {
      this.isLoadMore = true
      if (this.isLoadMore) {
        this.loadStatus = 'loading'
        setTimeout(() => {
          this.pageNo += 1
          /**
           * 此时pageNo的值为2，然而重新请求了一次，会覆盖之前的数据，那怎么办
           * 在请求第二页的时候，判断拿回来的数据是否超过请求数量，小于请求数量时，再次上拉加载的时候
           * 不在发起请求，大于等于110的时候再次判断请求下一页
           * 把分页拿回来的数据分别存储起来
           */
          this.getproductList()
        }, 3000);
      }
    }
  },
  onLoad () {
    this.getproductList()
    this.titleheight = 210
  },
  methods: {
    tabSelect (e) {
      const index = e.currentTarget.dataset.id
      switch (index) {
        case 0:
          this.getproductList(index, this.tabNav[index])
          break;
        case 1:
          this.getproductList(index, this.tabNav[index])
          break;
        case 2:
          this.getproductList(index, this.tabNav[index])
          break;
        case 3:
          break;
        default:
          break;
      }
      this.TabCur = e.currentTarget.dataset.id;
      this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
    },
    //推荐商品跳转
    toPromotion (e) {
      uni.showToast({
        title: e.title,
        icon: 'none'
      });
    },
    //商品跳转
    toGoods (e) {
      uni.navigateTo({
        url: '/pages/shop/goods_info',
        success: function () {
          uni.$emit('test', e)
        }
      });
    },
    //轮播图指示器
    swiperChange (event) {
      this.currentSwiper = event.detail.current;
    },
    InputBlur () {

    },
    InputFocus () {

    }
  }
};
</script>
<style lang="scss">
page {
  position: relative;
  background-color: #fff;
}

@font-face {
  font-family: 'HMfont-home';
  src: url('data:application/x-font-woff2;charset=utf-8;base64,')
    format('woff2');
}

.pullDown-effects {
  position: fixed;
  //top: calc(100upx - 36vw);
  top: 0;
  z-index: 9;
  width: 100%;
  height: 36vw;
  /*  #ifdef  APP-PLUS  */
  padding-top: var(--status-bar-height);

  /*  #endif  */
  image {
    width: 100%;
    height: 36vw;
  }
}
.goods_title {
  // position: relative;
  // bottom: 10px;
  // left: 0;
  // height: 200px;
  margin-bottom: 10px !important;
  .title {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80upx;
    color: #f47825;
    font-size: 30upx;
    margin-top: 10upx;

    image {
      width: 30upx;
      height: 30upx;
    }
  }
}
.swiper {
  width: 100%;
  margin-top: 10upx;
  display: flex;
  justify-content: center;

  .swiper-box {
    width: 92%;
    height: 30.7vw;

    overflow: hidden;
    border-radius: 15upx;
    box-shadow: 0upx 8upx 25upx rgba(0, 0, 0, 0.2);
    //兼容ios，微信小程序
    position: relative;
    z-index: 1;

    swiper {
      width: 100%;
      height: 30.7vw;

      swiper-item {
        image {
          width: 100%;
          height: 30.7vw;
        }
      }
    }

    .indicator {
      position: absolute;
      bottom: 20upx;
      left: 20upx;
      background-color: rgba(255, 255, 255, 0.4);
      width: 150upx;
      height: 5upx;
      border-radius: 3upx;
      overflow: hidden;
      display: flex;

      .dots {
        width: 0upx;
        background-color: rgba(255, 255, 255, 1);
        transition: all 0.3s ease-out;

        &.on {
          width: (100%/3);
        }
      }
    }
  }
}

.category-list {
  width: 100%;
  margin: 0 4%;
  padding: 0 0 30upx 0;
  border-bottom: solid 2upx #f6f6f6;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  .category {
    width: 25%;
    margin-top: 50upx;
    display: flex;
    flex-wrap: wrap;

    .img {
      width: 100%;
      display: flex;
      justify-content: center;

      image {
        width: 9vw;
        height: 9vw;
      }
    }

    .text {
      margin-top: 16upx;
      width: 100%;
      display: flex;
      justify-content: center;
      font-size: 24upx;
      color: #3c3c3c;
    }
  }
}

.banner {
  width: 92%;
  margin: 40upx 4%;

  image {
    width: 100%;
    height: 20vw;
    border-radius: 10vw;
    box-shadow: 0upx 5upx 25upx rgba(0, 0, 0, 0.3);
  }
}

.promotion {
  width: 92%;
  margin: 0 4%;

  .text {
    width: 100%;
    height: 60upx;
    font-size: 34upx;
    font-weight: 600;
    margin-top: -10upx;
  }

  .list {
    width: 100%;
    display: flex;
    justify-content: space-between;

    .column {
      width: 43%;
      padding: 15upx 3%;
      background-color: #ebf9f9;
      border-radius: 10upx;
      overflow: hidden;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

      .top {
        width: 100%;
        height: 40upx;
        display: flex;
        align-items: center;
        margin-bottom: 5upx;

        .title {
          font-size: 30upx;
        }

        .countdown {
          margin-left: 20upx;
          display: flex;
          height: 40upx;
          display: flex;
          align-items: center;
          font-size: 20upx;

          view {
            height: 30upx;
            background-color: #f06c7a;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #fff;
            border-radius: 4upx;
            margin: 0 4upx;
            padding: 0 2upx;
          }
        }
      }

      .left {
        width: 50%;
        height: 18.86vw;
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;

        .ad {
          margin-top: 5upx;
          width: 100%;
          font-size: 22upx;
          color: #acb0b0;
        }

        .into {
          width: 100%;
          font-size: 24upx;
          color: #aaa;
          margin-bottom: 5upx;
        }
      }

      .right {
        width: 18.86vw;
        height: 18.86vw;

        image {
          width: 18.86vw;
          height: 18.86vw;
        }
      }
    }
  }
}

.goods-list {
  background-color: #f4f4f4;
  .title {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80upx;
    color: #f47825;
    font-size: 30upx;
    margin-top: 10upx;

    image {
      width: 30upx;
      height: 30upx;
    }
  }

  .loading-text {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60upx;
    color: #979797;
    font-size: 24upx;
  }

  .product-list {
    width: 100%;
    padding: 0 4% 3vw 4%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .product {
      height: 220px;
      width: 48%;
      border-radius: 20upx;
      background-color: #fff;
      margin: 0 0 30upx 0;
      box-shadow: 0upx 5upx 25upx rgba(0, 0, 0, 0.1);

      .image {
        height: 120px;
      }

      image {
        width: 100%;
        border-radius: 20upx 20upx 0 0;
      }

      .name {
        width: 92%;
        padding: 10upx 4%;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        text-align: justify;
        overflow: hidden;
        font-size: 30upx;
      }

      .info {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        width: 92%;
        padding: 10upx 4% 10upx 4%;

        .price {
          color: #e65339;
          font-size: 30upx;
          font-weight: 600;
        }

        .slogan {
          color: #807c87;
          font-size: 24upx;
        }
      }
    }
  }
}
</style>
